import React from 'react';

class CanvasSizeSettings extends React.Component {
    render() {
        const { onClick } = this.props;

        return (

        	<div className="settings-area">
            	<button 
                    className="canvas-size-button"
                    onClick={ () => onClick(32) }
                >
                    32x32
                </button>
                
            	<button 
                    className="canvas-size-button"
                    onClick={ () => onClick(64) }
                >
                    64x64
                </button>

                <button 
                    className="canvas-size-button"
                    onClick={ () => onClick(128) }
                >
                    128x128
                </button>
            </div>
        )
    }
}

export default CanvasSizeSettings;