function moveFunction(frame, startX, startY, x, y) {
    const deltaX = x - startX;
    const deltaY = startY - y;
    const canvasSize = frame.length;

    if(deltaX > 0 ){
        frame = Array.from(Array(deltaX), () => new Array(canvasSize).fill(0)).concat(frame.slice(0, -deltaX));
    }

    if(deltaX < 0){
        frame = frame.slice(-deltaX).concat(Array.from(Array(-deltaX), () => new Array(canvasSize).fill(0)));
    }

    for (let i = 0; i < canvasSize; i++) {
        if (deltaY < 0) {
            frame[i] = Array(-deltaY).fill(undefined).concat(frame[i].slice(0, deltaY));
        }; 

        if (deltaY > 0) {
            frame[i] = frame[i].slice(deltaY).concat((Array(deltaY).fill(undefined)));
        }; 
    }

    return frame;
}

export default moveFunction;



