function colorSamePikselsFunction(frame, color, pikselSize, e) {
    const startX = Math.floor(e.offsetX / pikselSize);
    const startY = Math.floor(e.offsetY / pikselSize);
	const startColor = frame[startX][startY] ? frame[startX][startY].join() : undefined;

    frame.forEach((row, x) => {
        row.forEach((cell, y) => {
            const correntColor = frame[x][y] ? frame[x][y].join() : undefined;
            if(correntColor === startColor) frame[x][y] = color;
        })
    })
}

export default colorSamePikselsFunction; 
