function lineDraw(frame, color, startX, startY, x, y, isMirror = false) {
    let deltaX = Math.abs(startX - x);
    let deltaY = Math.abs(startY - y);
    let signX = x < startX ? 1 : -1;
    let signY = y < startY ? 1 : -1;
    let error = deltaX - deltaY;
    const center = frame.length / 2;

    do {
        let error2 = error * 2;

        frame[startX][startY] = frame[x][y] = color;

        if (isMirror) {
            let delta = -(x - center + 1);
            frame[center + delta][y] = color;
        }

        if (error * 2 > -deltaY) {
            error -= deltaY;
            x += signX;
        }

        if (error2 < deltaX) {
            error += deltaX;
            y += signY;
        }
        
    } while (x !== startX || y !== startY)

    return frame;
}

export default lineDraw;