import React from 'react';
import Settings from "./Settings/Settings";
import FramesPreview from "./FramesPreview/FramesPreview";
import FramesControl from "./FramesControl/FramesControl";
import ToolsControl from "./Tools/ToolsControl";
import CanvasContainer from "./Canvas/CanvasContainer";
import LayerSettings from "./LayerSettings/LayerSettings";
import CreateBaseFrame from "./utils/CreateBaseFrame";
import ColorPicker from "./Tools/ColorPicker";
import hexToRgb from "./utils/hexToRGB";
import makeFrameCopy from "./utils/makeFrameCopy";

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeToolId: 0,
            pikselSize: 24,
            mousePosition: { x: 0, y: 0 },
            canvasSize: 32,
            layers: [[CreateBaseFrame(32)]],
            currentFrame: 0,
            currentLayer: 0,
            mainColor: [0, 0, 0],
            secondaryColor: [116, 35, 160],
        }

        this.handleToolClick = this.handleToolClick.bind(this);
        this.handleFrameClick = this.handleFrameClick.bind(this);
        this.changeFrame = this.changeFrame.bind(this);
        this.addFrame = this.addFrame.bind(this);
        this.deleteFrame = this.deleteFrame.bind(this);
        this.copyFrame = this.copyFrame.bind(this);
        this.handleColorPickerClick = this.handleColorPickerClick.bind(this);
        this.swapColor = this.swapColor.bind(this);
        this.changeCanvasSize = this.changeCanvasSize.bind(this);
        this.addLayer = this.addLayer.bind(this);
        this.deleteLayer = this.deleteLayer.bind(this);
        this.changeCurrentLayer = this.changeCurrentLayer.bind(this);
    }

    render() {
        const { activeToolId,
            pikselSize,
            layers,
            currentFrame,
            mainColor,
            secondaryColor,
            canvasSize,
            currentLayer,
        } = this.state;

        return (
            <div
                className="app-main"
                onClick={this.mainHandleClick}
            >
                <div className="tools">
                    <ToolsControl
                        onClick={this.handleToolClick}
                        activeToolId={activeToolId}
                    />

                    <ColorPicker
                        onClick={this.handleColorPickerClick}
                        mainColor={mainColor}
                        secondaryColor={secondaryColor}
                        swapColor={this.swapColor}
                    />
                </div>

                <FramesControl
                    frames={layers[currentLayer]}
                    pikselSize={pikselSize}
                    onClick={this.handleFrameClick}
                    addFrame={this.addFrame}
                    currentFrame={currentFrame}
                    copyFrame={this.copyFrame}
                    deleteFrame={this.deleteFrame}
                />

                <CanvasContainer
                    activeToolId={activeToolId}
                    changeFrame={this.changeFrame}
                    frame={makeFrameCopy(layers[currentLayer][currentFrame])}
                    mainColor={mainColor}
                    secondaryColor={secondaryColor}
                    pikselSize={pikselSize}
                    changeMousePosition={this.changeMousePosition}
                    canvasSize={canvasSize}
                    currentFrame={currentFrame}
                    framesLength={layers[currentLayer].length}
                />

                <div className="preview-tools">
                    <FramesPreview
                        layers={layers}
                        pikselSize={pikselSize}
                    />

                    <LayerSettings
                        currentLayer={currentLayer}
                        layers={layers}
                        addLayer={this.addLayer}
                        deleteLayer={this.deleteLayer}
                        changeCurrentLayer={this.changeCurrentLayer}
                    />
                </div>

                <Settings onClick={this.changeCanvasSize} />
            </div>
        )
    }

    mainHandleClick() {
        const settings = document.querySelector(".settings");
        settings.classList.remove("activeMenu");
    }

    changeFrame(frame) {
        const { layers, currentFrame, currentLayer } = this.state;
        layers[currentLayer][currentFrame] = frame;
        this.setState({ layers });
    }

    handleToolClick(id) {
        this.setState({ activeToolId: id, });
    }

    handleColorPickerClick(id, color) {
        if (id === 0) this.setState({ mainColor: hexToRgb(color) });
        if (id === 1) this.setState({ secondaryColor: hexToRgb(color) });
    }

    swapColor() {
        const { secondaryColor, mainColor } = this.state;

        this.setState({
            mainColor: secondaryColor,
            secondaryColor: mainColor,
        })
    }

    handleFrameClick(id) {
        this.setState({ currentFrame: id, });
    }

    addFrame() {
        const { layers, canvasSize } = this.state;

        layers.forEach(frames => frames.push(CreateBaseFrame(canvasSize)))
        this.setState({ layers: layers });
    }

    copyFrame(id, e) {
        e.stopPropagation();

        const { layers } = this.state;

        layers.forEach(frames => {
            const clone = makeFrameCopy(frames[id]);
            frames.splice(id, 0, clone);
        });
        this.setState({ layers: layers });
    }

    deleteFrame(id, e) {
        e.stopPropagation();
        const { layers, currentLayer } = this.state;

        layers.forEach(frames => {
            frames.splice(id, 1);
        });

        this.setState({
            currentFrame: layers[currentLayer].length === id ? id - 1 : id,
            layers: layers,
        })

    }

    changeCanvasSize(newCanvasSize) {
        const { canvasSize, layers, currentLayer, pikselSize } = this.state;
        if (newCanvasSize === canvasSize) return;
        const delta = Math.abs(newCanvasSize - canvasSize) / 2;
        const pikselDelta = canvasSize / newCanvasSize;
        const framesLength = layers[currentLayer].length;
        const mainCanvasContainer = document.querySelector('.main-canvas-container');

        mainCanvasContainer.style.backgroundSize = `${pikselSize * pikselDelta}px ${pikselSize * pikselDelta}px`;

        for (let j = 0; j < layers.length; j++) {
            if (newCanvasSize > canvasSize) {

                for (let i = 0; i < framesLength; i++) {
                    const frame = layers[j][i];

                    for (let i = 0; i < canvasSize; i++) {
                        frame[i] = Array(delta).concat(frame[i]);
                        frame[i] = frame[i].concat(Array(delta));
                    }

                    for (let i = 0; i < delta; i++) {
                        frame.unshift(Array(delta * 2));
                        frame.push(Array(delta * 2));
                    }
                }
            }

            if (newCanvasSize < canvasSize) {
                for (let i = 0; i < framesLength; i++) {

                    const frame = layers[j][i];
                    frame.splice(0, delta);
                    frame.splice(newCanvasSize, delta);

                    for (let i = 0; i < newCanvasSize; i++) {
                        frame[i].splice(0, delta);
                        frame[i].splice(newCanvasSize, delta);
                    }
                }
            }
        }

        this.setState({
            pikselSize: pikselSize * pikselDelta,
            canvasSize: newCanvasSize,
            layers: layers,
        })
    }

    addLayer() {
        const { layers, canvasSize } = this.state;
        const framesCount = layers[0].length;
        const newLayer = [];

        for (let i = 0; i < framesCount; i++) {
            newLayer.push(CreateBaseFrame(canvasSize));
        }

        layers.push(newLayer);
        this.setState({ layers: layers });
    }

    deleteLayer(id, e) {
        e.stopPropagation();
        const { layers } = this.state;
        layers.splice(id, 1);
        this.setState({
            currentLayer: layers.length === id ? id - 1 : id,
            layers: layers,

        });
    }

    changeCurrentLayer(id, e) {
        e.stopPropagation();

        this.setState({
            currentLayer: id,
            currentFrame: 0,
        });
    }
}

export default Main;