import React from 'react';
import ToolButton from "./ToolButton"
import { faPen, faFillDrip, faEraser, faThumbtack, faRadiation, faSun, faGripLinesVertical, faUndoAlt, faChessBoard } from '@fortawesome/free-solid-svg-icons'
import { faCircle, faSquare, faHandPaper } from '@fortawesome/free-regular-svg-icons'
import key from 'weak-key';

class ToolsControl extends React.Component {

    render() {
        const Icons = [faPen,
            faEraser,
            faFillDrip,
            faCircle,
            faSquare,
            faThumbtack,
            faRadiation,
            faSun,
            faGripLinesVertical,
            faUndoAlt,
            faHandPaper,
            faChessBoard,
        ];
        const { onClick, activeToolId } = this.props;

        return (
            <div className="tools-controle">
                {
                    Icons.map((item, i) =>
                        <ToolButton
                            key={key(item)}
                            icon={item}
                            onClick={onClick}
                            id={i}
                            activeToolId={activeToolId}
                        />
                    )
                }
            </div>
        )
    }
}

export default ToolsControl;