function createRectangle(frame, e, pikselSize, color, startX, startY) {

    const x = Math.floor(e.offsetX / pikselSize);
    const y = Math.floor(e.offsetY / pikselSize);

    const deltaX = startX - x;
    const deltaY = startY - y;

    const minY = Math.min(startY, y);
    const minX = Math.min(startX, x);

    for (let i = minY; i <= minY + Math.abs(deltaY); i++) {
        frame[x + deltaX][i] = color;
        frame[x][i] = color;
    }

    for (let i = minX; i <= minX + Math.abs(deltaX); i++) {
        frame[i][y] = color
        frame[i][y +  deltaY] = color
    }
    return frame;
}

export default createRectangle;