import React from 'react';

class FrameInfo extends React.Component {
    render() {
        const {
            mousePositionX,
            mousePositionY,
            canvasSize,
            framesLength,
            currentFrame, 
        } = this.props;

        return (
            <div className="frame-info">
                <span>{mousePositionX}x{mousePositionY}</span>
                <span>{currentFrame + 1}/{framesLength}</span>
                <span>[{canvasSize}x{canvasSize}]</span>
            </div>
        )
    }
}

export default FrameInfo;