function createCircle(frame, e, pikselSize, color, startX, startY) {

    const currentX = Math.floor(e.offsetX / pikselSize);
    const currentY = Math.floor(e.offsetY / pikselSize);
    const R = Math.max(Math.abs(currentX - startX),  Math.abs(currentY - startY));
    const clone = JSON.parse(JSON.stringify(frame));
    const canvasSize = frame.length;

    let x = 0;
    let y = R;
    let delta = 1 - 2 * R;
    let error = 0;
    

    while (y >= 0) {
        const leftIndex = startX - x;
        const rightIndex = startX + x;
        const upIndex = startY - y;
        const downIndex = startY + y;

        const leftBorderCrossed = leftIndex < 0;
        const rightBorderCrossed = rightIndex >= canvasSize;
        const upBorderCrossed = upIndex < 0;
        const downBorderCrossed = downIndex >= canvasSize;

        if (!(rightBorderCrossed || downBorderCrossed)) clone[rightIndex][downIndex] = color;
        if (!(rightBorderCrossed || upBorderCrossed)) clone[rightIndex][upIndex] = color;
        if (!(leftBorderCrossed || downBorderCrossed)) clone[leftIndex][downIndex] = color;
        if (!(leftBorderCrossed || upBorderCrossed)) clone[leftIndex][upIndex] = color;

        error = 2 * (delta + y) - 1;
        if (delta < 0 && error <= 0) {
            delta += 2 * ++x + 1;
            continue;
        }

        error = 2 * (delta - x) - 1;
        if (delta > 0 && error > 0) {
            delta += 1 - 2 * --y;
            continue;
        }

        x++;
        delta += 2 * (x - y);
        y--;
    }
    return clone;
}

export default createCircle;