import React from 'react';

class Header extends React.Component {
    render() {

        const { isLandingPage, disActiveLandingPage, activeLandingPage } = this.props;
        return (
            <div className="app-header">
                <h1
                    onClick={ activeLandingPage }
                >
                    Piskel
                </h1>

                <div className="header-buttons-container">
                    {isLandingPage &&
                        <button
                            className="create-sprite-button"
                            onClick={disActiveLandingPage}
                        >
                            Create sprite
                        </button>
                    }

                    <button className="create-sprite-button">
                        Sing in
                    </button>
                </div>


            </div>
        )
    }
}

export default Header;