function renderCanvas(canvas, frame, pikselSize, clearCanvas = true) {
    const context = canvas.getContext("2d");
    if(clearCanvas) context.clearRect(0, 0, canvas.width, canvas.height);
    frame.forEach((item, i) => {
        item.forEach((item, j) => {
        	if(!item) return;
            context.fillStyle = `rgb(${item.join()})`;
			context.fillRect(pikselSize * i, pikselSize * j, pikselSize, pikselSize);
        })
    })
}

export default renderCanvas;
