function bucketFunction(frame, color, pikselSize, e) {
    const startX = Math.floor(e.offsetX / pikselSize);
	const startY = Math.floor(e.offsetY / pikselSize);
	const canvasSize = frame.length;
	const startColor = frame[startX][startY] ? frame[startX][startY].join() : undefined;
	if(startColor === color.join()) return;
    const stack = [[startX, startY]];

    while(stack.length){
    	const position = stack.pop();
    	const x = position[0];
    	const y = position[1];

    	if ((x >= 0 && x < canvasSize) && (y >= 0 && y < canvasSize)) {
    		const correntColor = frame[x][y] ? frame[x][y].join() : undefined;
    		if (startColor === correntColor) {

    			frame[x][y] = color;
    			stack.push([x - 1, y], [x + 1, y], [x, y - 1], [x, y + 1]);
    		}
    	}
    }
}

export default bucketFunction; 
