function erotateClockwis(frame) {
    var n = frame.length;
    for (let i = 0; i < n / 2; i++) {
        for (let j = i; j < n - i - 1; j++) {
            let tmp = frame[i][j];
            
            frame[i][j] = frame[n - j - 1][i];
            frame[n - j - 1][i] = frame[n - i - 1][n - j - 1];
            frame[n - i - 1][n - j - 1] = frame[j][n - i - 1];
            frame[j][n - i - 1] = tmp;
        }
    }

    return frame;
}


export default erotateClockwis; 
