import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons';

class Layer extends React.Component {

  render() {

    const { id, deleteLayer, changeCurrentLayer, currentLayer, layers } = this.props;
    return (
      <div
        id={id}
        onClick={(e) => changeCurrentLayer(id, e)}
        className={`${currentLayer === id ? "active " : ""}layer-view`}
      >
        Layer {id + 1}

        {layers.length > 1 && 
          <button
            className="delete-layer-button"
            onClick={(e) => deleteLayer(id, e)}
          >
            <FontAwesomeIcon icon={faTrash} />
          </button>
        }

        {layers.length > 1 && 
          <button
            className="delete-layer-button"
            onClick={(e) => deleteLayer(id, e)}
          >
            <FontAwesomeIcon icon={faTrash} />
          </button>
        }

        {layers.length > 1 && 
          <button
            className="delete-layer-button"
            onClick={(e) => deleteLayer(id, e)}
          >
            <FontAwesomeIcon icon={faTrash} />
          </button>
        }

      </div>
    )
  }
}

export default Layer;