import React from 'react';
import Header from "./Header.js";
import Main from "./Main.js";
import LandingPage from "./LandingPage";

class App extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isLandingPage: true,
    }

    this.activeLandingPage = this.activeLandingPage.bind(this);
    this.disActiveLandingPage = this.disActiveLandingPage.bind(this);
  }

  render() {

    const  { isLandingPage } = this.state;

    return (
      <div className="App">
        <Header
          disActiveLandingPage={this.disActiveLandingPage}
          activeLandingPage={this.activeLandingPage}
          isLandingPage={this.state.isLandingPage}
        />

        {isLandingPage &&
          <LandingPage />
        }

        {!isLandingPage && <Main />}
      </div>
    );
  }

  activeLandingPage() {
    this.setState({
      isLandingPage: true,
    });
  }

  disActiveLandingPage() {
    this.setState({
      isLandingPage: false,
    });
  }
}

export default App;
