import React from 'react';
import renderCanvas from "../utils/renderCanvas"



class PreviewArea extends React.Component {

    constructor(props) {
        super(props);

        this.animateFramesPreview = this.animateFramesPreview.bind(this);
        this.canvas = React.createRef();
    }
    render() {
        return (
            <canvas 
                className="preview-canvas"
                width={ 384 }
                height={ 384 }
                ref={ this.canvas }
            >
            </canvas> 
        )
    }

    componentDidUpdate() {
        clearTimeout(this.timer);
        this.animateFramesPreview();
        
    }

    animateFramesPreview() {
        if (this.props.fpsInputValue === 0) return;
        const canvas = this.canvas.current;
        const { layers, fpsInputValue, pikselSize } = this.props;
        let x = 0;

        function Frame(){
            if (x >= layers[0].length) x = 0;
            renderCanvas( canvas, layers[0][x], pikselSize / 2);

            for(let i = 1; i < layers.length; i++){
                renderCanvas( canvas, layers[i][x], pikselSize / 2, false );
            }

            x++ 
        }

        this.timer = setInterval(() => Frame(), 1000 / fpsInputValue);

        // if (this.props.fpsInputValue === 0) return;
        // const canvas = this.canvas.current;
        // const { frames, fpsInputValue, pikselSize } = this.props;
        // let x = 0;

        // function Frame(){
        //     if (x >= frames.length) x = 0;
        //     renderCanvas( canvas, frames[x], pikselSize / 2 );
        //     x++
        // }


        // let x = 0;
        // let startTime = new Date();
        // const canvas = this.canvas.current;
        
        // const Frame = () => {

        //     requestAnimationFrame(Frame);
        //     const time = new Date();
            
        //     const { frames, fpsInputValue, pikselSize } = this.props;

        //     if (time - startTime < 1000 / fpsInputValue) return;
        //     startTime = time;
        //     if (this.props.fpsInputValue === 0) return;
        //     if (x >= frames.length) x = 0;
        //     renderCanvas( canvas, frames[x], pikselSize / 2 );
        //     x++;
        // }
        // Frame();
    }
}

export default PreviewArea;