import React from 'react';
import Frame from "./Frame";
import AddFrameButton from "./AddFrameButton"
import key from 'weak-key';



class FramesControl extends React.Component {

    render() {
    	const { 
            onClick, 
            frames, 
            currentFrame, 
            copyFrame, 
            deleteFrame, 
            addFrame,
            pikselSize, 
        } = this.props;
        
        return (
            <div className="frames-controle">
                {frames.map((frame, i) => 
                    <Frame 
                        onClick={ onClick } 
                        frames={ frames } 
                        key={ key(frame) }
                        id={ i } 
                        active={ currentFrame === i }
                        copyFrame={ copyFrame }
                        deleteFrame={ deleteFrame }
                        pikselSize={pikselSize}
                    />)
                }

                <AddFrameButton 
                	onClick={ addFrame }
                />
            </div>
        )
    } 
}

export default FramesControl;