import React from 'react';
import renderCanvas from "../utils/renderCanvas";
import makeFrameCopy from "../utils/makeFrameCopy";
import createRectangle from "../utils/createRectangle";
import createCircle from "../utils/createCircle";
import bucketFunction from "../utils/bucketFunction";
import lineDraw from "../utils/lineDraw";
import lighten from "../utils/lighten";
import getCoordinates from "../utils/getCoordinates";
import colorSamePikselsFunction from "../utils/colorSamePikselsFunction";
import erotateClockwis from "../utils/erotateClockwis";
import moveFunction from "../utils/moveFunction";
import dithering from "../utils/dithering";

class MainCanvas extends React.PureComponent {

    constructor(props) {
        super(props);

        this.penOrEraserOrMirror = this.penOrEraserOrMirror.bind(this);
        this.canvas = React.createRef();
        this.chooseToolFunction = this.chooseToolFunction.bind(this);
        this.rectangleAndCircle = this.rectangleAndCircle.bind(this);
        this.stroke = this.stroke.bind(this);
        this.lighten = this.lighten.bind(this);
        this.rotateFrame = this.rotateFrame.bind(this);
        this.moveFrame = this.moveFrame.bind(this);
        this.ditheringFrame = this.ditheringFrame.bind(this);
    }

    render() {
        return (
            <canvas
                onMouseDown={this.chooseToolFunction}
                onContextMenu={e => e.preventDefault()}
                className="main-canvas"
                width="768"
                height="768"
                ref={this.canvas}
            >
            </canvas>
        )
    }

    componentDidUpdate() {
        const { frame, pikselSize } = this.props;
        const canvas = this.canvas.current;
        renderCanvas(canvas, frame, pikselSize);
    }

    chooseToolFunction(e) {
        const { activeToolId } = this.props;

        switch (activeToolId) {
            case 0:
            case 1:
            case 8:
                this.penOrEraserOrMirror(e);
                break;
            case 2:
                this.bucketArea(e);
                break;
            case 3:
            case 4:
                this.rectangleAndCircle(activeToolId, e);
                break;
            case 5:
                this.stroke(e);
                break;
            case 6:
                this.colorSamePiksels(e);
                break;
            case 7:
                this.lighten(e);
                break;
            case 9:
                this.rotateFrame();
                break;
            case 10:
                this.moveFrame(e);
                break;
            case 11:
                this.ditheringFrame(e);
                break;
            default:
                break;
        }
    }

    ditheringFrame(e) {

        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
        } = this.props;

        const startCoords = getCoordinates(e, pikselSize);
        let clone = makeFrameCopy(frame);
        let colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;

        dithering(clone, colorRGBA, startCoords.x, startCoords.y)
        renderCanvas(canvas, frame, pikselSize);

        canvas.onmousemove = (e) => {
            const currentCoords = getCoordinates(e, pikselSize);

            clone = dithering(clone, colorRGBA, currentCoords.x, currentCoords.y);
            renderCanvas(canvas, clone, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(clone);
        }

    }

    moveFrame(e) {
        const { frame, pikselSize, changeFrame } = this.props;
        const canvas = this.canvas.current;
        const startCoords = getCoordinates(e, pikselSize);

        let clone = makeFrameCopy(frame);
        canvas.onmouseup = null;

        canvas.onmousemove = (e) => {
            let startFrame = makeFrameCopy(frame);
            const currentCoords = getCoordinates(e, pikselSize);
            clone = moveFunction(startFrame, startCoords.x, startCoords.y, currentCoords.x, currentCoords.y);
            renderCanvas(canvas, clone, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(clone);
        }
    }

    rotateFrame() {
        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame, changeFrame } = this.props;
        const clone = erotateClockwis(frame);

        changeFrame(clone);
    }

    colorSamePiksels(e) {
        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
        } = this.props;

        const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
        colorSamePikselsFunction(frame, colorRGBA, pikselSize, e.nativeEvent);
        changeFrame(frame);
    }

    stroke(e) {

        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
        } = this.props;

        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
        const startCoords = getCoordinates(e, pikselSize);
        let clone = makeFrameCopy(frame);

        lineDraw(clone, colorRGBA, startCoords.x, startCoords.y, startCoords.x, startCoords.y)
        renderCanvas(canvas, frame, pikselSize);

        canvas.onmousemove = (e) => {
            const startFrame = makeFrameCopy(frame);
            const currentCoords = getCoordinates(e, pikselSize);

            clone = lineDraw(startFrame, colorRGBA, startCoords.x, startCoords.y, currentCoords.x, currentCoords.y)
            renderCanvas(canvas, clone, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(clone);
        }
    }


    rectangleAndCircle(activeToolId, e) {
        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
        } = this.props;

        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
        const startCoords = getCoordinates(e, pikselSize);
        const chozenFunction = activeToolId === 4 ? createRectangle : createCircle;
        let clone = makeFrameCopy(frame);

        lineDraw(clone, colorRGBA, startCoords.x, startCoords.y, startCoords.x, startCoords.y)
        renderCanvas(canvas, frame, pikselSize);

        canvas.onmousemove = (e) => {
            const startFrame = makeFrameCopy(frame);
            clone = chozenFunction(startFrame, e, pikselSize, colorRGBA, startCoords.x, startCoords.y);
            renderCanvas(canvas, clone, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(clone);
        }
    }

    bucketArea(e) {
        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
        } = this.props;

        const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
        bucketFunction(frame, colorRGBA, pikselSize, e.nativeEvent);
        changeFrame(frame);
    }

    penOrEraserOrMirror = (e) => {
        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame,
            mainColor,
            secondaryColor,
            pikselSize,
            changeFrame,
            activeToolId,
        } = this.props;

        const startCoords = getCoordinates(e, pikselSize);
        const isMirror = activeToolId === 8;
        let coordinates = [startCoords.x, startCoords.y];
        let colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;

        if (activeToolId === 1) { colorRGBA = undefined };

        lineDraw(frame, colorRGBA, startCoords.x, startCoords.y, startCoords.x, startCoords.y, isMirror);
        renderCanvas(canvas, frame, pikselSize);

        canvas.onmousemove = (e) => {
            let currentCoords = getCoordinates(e, pikselSize);

            coordinates.push(currentCoords.x, currentCoords.y);
            if (coordinates.length > 4) coordinates.splice(0, 2);

            lineDraw(frame, colorRGBA, ...coordinates, isMirror);
            renderCanvas(canvas, frame, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(frame);
        }
    }

    lighten = (e) => {
        const canvas = this.canvas.current;
        canvas.onmouseup = null;
        const { frame, pikselSize, changeFrame } = this.props;
        const startCoords = getCoordinates(e, pikselSize);
        let colorRGBA = lighten(frame[startCoords.x][startCoords.y]);

        lineDraw(frame, colorRGBA, startCoords.x, startCoords.y, startCoords.x, startCoords.y);
        renderCanvas(canvas, frame, pikselSize);

        canvas.onmousemove = (e) => {
            const currentCoords = getCoordinates(e, pikselSize);
            colorRGBA = lighten(frame[currentCoords.x][currentCoords.y]);

            lineDraw(frame, colorRGBA, currentCoords.x, currentCoords.y, currentCoords.x, currentCoords.y);
            renderCanvas(canvas, frame, pikselSize);
        }

        canvas.onmouseup = () => {
            canvas.onmousemove = null;
            changeFrame(frame);
        }
    }
}

export default MainCanvas;