import React from 'react';

class ColorButton extends React.Component {
    constructor(props){
        super(props);

        this.input = React.createRef();
    }

    render() {
        const { buttonClass, onClick, id, color, value } = this.props;

        return (
            <button 
                className={ buttonClass }
                style={{ backgroundColor: `rgba(${ color.join() })` }}
                onClick={ () => this.input.current.click() }
            >
                <input 
                    type="color"
                    onChange={ () => onClick(id, this.input.current.value)}
                    ref={ this.input }
                    value={ value }
                >
                </input>
            </button>
        )
    }
}

export default ColorButton;